	import uniIcon from "@/components/uni-icons/uni-icons.vue";
	const JsyServer = require("services/jsy-server.js");
	export default {
		components:{
			uniIcon
		},
		data() {
			return {
				phone: '',
				password: '',
				passType: 'password',
				isPhoneClear: false,
				isPassClear: false,
				countdown:'获取验证码',
				isDisplay: true,
				disabled:true
			};
			
			},
		methods:{
			backto:function(){
				uni.navigateBack({
					delta: 1
				});
			},
			showPass: function(){
				this.passType = this.passType==='password'?'text':'password'
			},
			clearPhone: function(){
				this.phone = ''
			},
			clearPass: function(){
				this.password =''
			},
			showCloseIcon:function(e){
				if (e.target.value){
					this.isPhoneClear = true
				}else {
					this.isPhoneClear = false
				}
			},
			showPassClearIcon: function(e){
				if(e.target.value) {
					this.isPassClear = true
				}else {
					this.isPassClear = false
				}
				
			},
			
			getValidCode:function(){
				if (!this.phone){
					uni.showToast({
						title: '请输入手机号码',
						icon: 'none'
					});
					return;
				}
				let _data ={
					phone: this.phone
				}
				JsyServer.verification(_data).then(res => {
				  console.log(res);
				  uni.showToast({
				  	title: '验证码已发送',
					icon: 'none'
				  });
				  
					
				}).catch(err => {
				  wx.showToast({
				    title: err.data.errMsg,
				    icon: 'none'
				  });
				});		
			},
			countDown(){
                var that = this;
                if(!that.countdown){                    
                    that.disabled = false;
                    that.timestatus = false;
                    that.countdown = '获取验证码';
                    clearInterval(that.clear);
                }else{
                    --that.countdown;
                }
            },
			formSubmit: function(e){
				let data = e.detail.value;
				let code = data.code;
				delete data.code;
				console.log(data);
				
				if(!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(data.phone))){
					uni.showToast({
						title: '请输入正确电话号码！',
						icon: 'none',
						duration: 2000
					});
					return ;
				}
				if(!data.password || data.password.length<6){
					uni.showToast({
						title: '请输入至少六位密码',
						icon: 'none',
						duration: 2000
					});
					return ;
				}
				
				if(!code){
					uni.showToast({
						title: '验证码不能为空',
						icon: 'none',
						duration: 2000
					});
					return ;
				}
				let _data = { 
					phone: data.phone,
					password: data.password,
					verification: code
				}
				console.log(_data)
				JsyServer.chanage_password(_data).then(res => {
				  console.log(res);
				  uni.showToast({
				  	title: '修改成功',
					icon: 'none'
				  });
					
				  uni.navigateBack({
				  	delta: 1
				  });
				}).catch(err => {
				  wx.showToast({
				    title: err.data.errMsg,
				    icon: 'none'
				  });
				});		
				
			}
		
		}
	}
